export default {
	data() {
		return {
			hasSendCode: false,
			codeSecond: 60,
			mobile: '',
			code: '',
			invitationCode:""
		}
	},
	methods: {
		clearIcon() {
			const that = this;
			that.mobile = "";
		},
		getCode() {
			const that = this;
			uni.showLoading({
				title:"请稍等..."
			})
			that.$api.isphonecanuse({
				"mobile":that.mobile
			}).then(res=>{
				if(res.success){
					that.$api.sendvcode({
						"mobile":that.mobile,
						"sendType":1
					}).then(res2=>{
						uni.hideLoading();
						if(res2.success){
							that.timeCount();
							uni.showToast({
								title: "验证码发送成功",
								icon: "none"
							})
						}
						else{
							uni.showToast({
								title: res2.message,
								icon: "none"
							})
						}
					})
				}
				else{
					uni.hideLoading();
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
			
		},
		timeCount(){
			const that = this;
			that.hasSendCode = true;
			var interval = setInterval(() => {
				--that.codeSecond
			}, 1000)
			setTimeout(() => {
				clearInterval(interval)
				that.hasSendCode = false;
				that.codeSecond=60;
			}, 60000)
		},
		next(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/setPassword",
				query:{
					code:that.code,
					mobile:that.mobile,
					invitationCode:that.invitationCode
				}
			})
		}
	}
}
